#app4{

}
@keyframes change{
    0%{
        background: red;
    }
    100%{
        background: #000000;
    }
}
#app4 .circle{
    width: 20vw;
    height: 20vw;
    border-radius: 50%;
    border: 1px solid red;
    
}
#app4 .circle.active{
    animation:  change 1s infinite alternate;
}